export function transliterate(text) {
 // use iso 9
 const mapping = {
  '': 'A', '': 'B', '': 'V', '': 'G', '': 'D',
  '': 'E', '': '', '': '', '': 'Z', '': 'I',
  '': 'J', '': 'K', '': 'L', '': 'M', '': 'N',
  '': 'O', '': 'P', '': 'R', '': 'S', '': 'T',
  '': 'U', '': 'F', '': 'Ch', '': 'C', '': '',
  '': '', '': '', '': 'Y', '': 'E', '': 'Ju',
  '': 'Ja', '': '\'', '': '\'',
  '': 'a', '': 'b', '': 'v', '': 'g', '': 'd',
  '': 'e', '': '', '': '', '': 'z', '': 'i',
  '': 'j', '': 'k', '': 'l', '': 'm', '': 'n',
  '': 'o', '': 'p', '': 'r', '': 's', '': 't',
  '': 'u', '': 'f', '': 'ch', '': 'c', '': '',
  '': '', '': '', '': 'y', '': 'e', '': 'ju',
  '': 'ja', '': '\'', '': '\''
};
  // Perform transliteration by mapping each character
  return [...text].map(char => mapping[char] || char).join('');
}
